import React, { useState, useCallback } from 'react'
import { useTransition, animated, config } from 'react-spring'

const img_urls = [
  'https://firebasestorage.googleapis.com/v0/b/popo-app-57132.appspot.com/o/EfT1MfmVAAAXLLR_600x600.jpg?alt=media&token=f3fabe51-e9cf-4861-874f-2cba10dd546c' ,
  'https://firebasestorage.googleapis.com/v0/b/popo-app-57132.appspot.com/o/EfT1O_bUMAAqE5H_600x600.jpg?alt=media&token=f6f256b4-914a-4c36-947b-bf7210860d3c' ,
  'https://firebasestorage.googleapis.com/v0/b/popo-app-57132.appspot.com/o/EfT1SQEUwAIsini_600x600.jpg?alt=media&token=f16bf9f7-0246-4c7a-88e1-734c45363b5a' ,
  'https://firebasestorage.googleapis.com/v0/b/popo-app-57132.appspot.com/o/EfT1Q8sU8AoW4ny_600x600.jpg?alt=media&token=09beb21f-5921-4400-bdfd-10dc64186d53' 
]
const img_cls="fixed rounded-lg ml-5 mt-5 w-3/4 md:w-auto md:max-h-1/2 border-black border-4 "
const images = [
  ({ style }) => <animated.img src={img_urls[0]} alt="" style={style} class={img_cls}/>,
  ({ style }) => <animated.img src={img_urls[1]} alt="" style={style} class={img_cls}/>,
  ({ style }) => <animated.img src={img_urls[2]} alt="" style={style} class={img_cls}/>,
  ({ style }) => <animated.img src={img_urls[3]} alt="" style={style} class={img_cls}/>
];

const Test = () => {
  const [index, setIndex] = useState(0);
  const onClick = useCallback(() => setIndex(state => (state + 1) % 4), []);

  const transitions = useTransition(index, p => p, {
    from: { opacity: 0, transform: "translateX(100%)" },
    enter: item => async (next, cancel) => {
      await new Promise(resolve => setTimeout(resolve, 500));
      await next({  opacity: 1, transform: "translateX(0%)" })
    },
    leave: { opacity: 0, transform: "translateX(-150%)" },
    config: config.slow,
  });

  return (
    <div onClick={onClick} class="p-3">
      <span>2020/08/13</span>
      {transitions.map(({ item, props, key }) => {
        const Image = images[item];
        return <Image key={key} style={props} />;
      })}
    </div>
  );
};

export default Test